import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { setParticipantId } from '../../actions/participantActions';

import './Login.css';

function Login() {
  const dispatch = useDispatch();
  const history = useHistory();
  const [pid, setPid] = useState('');
  const [error, setError] = useState('');

  const checkPID = (pid) => {
    const myRe = /^PID\d{5}$/;
    if (pid === '') {
      setError("You need to enter a participant ID");
    } else if (myRe.test(pid) === false) {
      setError("You must enter a valid PID")
    } else {
      dispatch(setParticipantId(pid))
      history.push('/turnip')
    }
  }

  return (
    <div className="main">
      <div className="login-container">
        <div className="login-box">
          <h1 className="title">Welcome to the dial testing portion of the study!</h1>
          <div className="input-container">
            <p style={{ color: 'black' }} >Please enter your Participant ID:</p>
            
            <input
              type="text" 
              placeholder="PID12345"
              onChange={e => setPid(e.target.value)}
              value={pid}
              className="pid-input"/>

            { error ? <p style={{ color: "red", marginBottom: 5 }}>{ error }</p> : null }

            <button onClick={() => checkPID(pid) } className="submit-button">
              Enter
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Login;